
.backgroundProfile{
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    background-color: rgb(161, 161, 161);

}
.sendlogo{
    margin-top: 3px;
    width: 30%;
    object-fit: cover;
}
.ProfileCard{
    background-color: black;
    height: 30rem ;
    width: 50rem;
    color: white;
    margin: 5rem  0;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.User-Name-photo{
    flex: 1;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.stats-card{
    flex: 1;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
}
.btn-div_edit{
    margin-top: 1rem;
    display: flex;
    justify-content: center;
}
.btn-edit-profile{
    height: auto;
    background-color: rgb(161, 161, 161);
    padding: 0.6rem 1.5rem;
    border: none;
    font-size: 1.1rem;
    cursor: pointer;
    border-radius: 15px;
}
.ScoreList{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width:90%;
}
.Each-score{
    background-color: rgb(161, 161, 161);
    color: black;
    border-radius: 15px;
    width: 15%;
    padding: 0.5rem 0.7rem ;
    text-align: center;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.scorelistdiv{
    width: 100%;
    display: flex;
    text-align: center;
    flex-direction: column;
    align-items: center;
}
.menuberger{
    display: flex;
    justify-content: space-around;
   
    padding-bottom: 1rem;
    width: 100%;
}
.menuinprofile{
    text-decoration: none;
    border-bottom:  #fff solid ;
}
@media only screen and (max-width:700px)  {
    .ScoreList,.User-Name-photo{
        flex-direction: column;
        justify-content: space-around;
        height: 90%;
        
    }
    .stats-card,.User-Name-photo{
        flex:1;
    }
    .ProfileCard{
        width: 100%;
        height: 50rem;
    }
    .Header{
        width:100%;
        overflow: hidden;
    }
    .Each-score{
        margin-top: 10px;
        width: 30%;
    }
}